import { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import OAuth from './OAuth';
import unify from '../../images/logo.png'
export default function SignUp() {
  const [formData, setFormData] = useState({});
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const [msg, setMsg] = useState("");
  const AUTH_SIGNUP = process.env.REACT_APP_AUTH_SIGNUP;
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      setError(false);
      const res = await fetch(AUTH_SIGNUP, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });
      const data = await res.json();
      console.log(data);
      setLoading(false);
      if (data.message === "Invalid credentials") {
        setMsg(data.message)
        setError(true);
        return;
      }
      navigate('/sign-in');
    } catch (error) {
      setLoading(false);
      setError(true);
    }
  };
  return (
    <div className="absolute inset-0 flex flex-col justify-center items-center min-h-screen" style={{ top: '4rem' }}>
      <div className="d-flex border border-5 justify-content-evenly shadow p-3 mb-5 bg-body rounded">
        <div className='max-w-lg mx-auto p-5 mt-5 d-none d-md-block'>

          <img src={unify}></img>
          <div className='mt-5'>
            <OAuth />

          </div>
        </div>
        <div className='max-w-lg p-5' style={{ backgroundColor: 'lavender' }}>
          <h1 className='text-3xl text-center font-semibold my-7'>Sign Up</h1>
          <form onSubmit={handleSubmit} className='flex flex-col gap-4'>
            <input
              type='text'
              placeholder='Username'
              id='username'
              className='bg-slate-100 p-3 rounded-lg'
              onChange={handleChange}
            />
            <input
              type='email'
              placeholder='Email'
              id='email'
              className='bg-slate-100 p-3 rounded-lg'
              onChange={handleChange}
            />
            <input
              type='password'
              placeholder='Password'
              id='password'
              className='bg-slate-100 p-3 rounded-lg'
              onChange={handleChange}
            />
            <button
              disabled={loading}
              className='bg-slate-700 text-white p-3 rounded-lg uppercase hover:opacity-95 disabled:opacity-80'
            >
              {loading ? 'Loading...' : 'Sign Up'}
            </button>
            <button className='d-md-none'>

              <OAuth />
            </button>
          </form>
          <div className='flex gap-2 mt-5'>
            <p>Have an account?</p>
            <Link to='/sign-in'>
              <span className='text-blue-500'>Sign in</span>
            </Link>
          </div>
          <p className='text-red-700 mt-5'>{error && 'Something went wrong!'}</p>
        </div>
      </div>
    </div>
  );
}